function registerManufacturer(formData) {
  try {
    if (getAdminUtils("admin_data").clientId) {
      var data_ = {
        ...formData,
        clientId: getAdminUtils("admin_data").clientId,
      };
    }
    return async (dispatch) => {
      const data = await Connector.post(
        `/manufacturers`,
        getAdminUtils("admin_data").clientId ? data_ : formData
      );
      console.log(data);
      notify("Successfully created");
      reloadPage();
    };
  } catch (err) {
    notify("failed to process");
  }
}
